<!-- 个人健康档案 -->
<template>
	<div id="personal-doc">
		<div class="head">
			<span><img :src="img2"></span><label>个人健康档案</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'personal-doc',
		data(){
			return{
				userId:"",
				img2:require("../assets/jiankang.png"),
				contents:{
					"档案状态":"",
					"档案编号":"",
					"建档机构":"",
					"管理机构":"",
					"责任医生":"",
					"糖尿病标志":"",
					"高血压标志":"",
					//"高血脂标志":"xxx",
					"高血脂高危等级":""
				}
				
			}	
		},
		methods:{
			 
				},
		mounted(){
						var head = document.getElementsByClassName("head");
						var content = document.getElementsByClassName("content");
						head[0].style.backgroundColor = "#fff";
						content[0].style.backgroundColor = "#fff";
						document.body.style.backgroundColor = "#f0f0f0";
						var key = document.getElementsByClassName("key");
						key[0].style.color = "rgb(18,183,137)" ;
						key[1].style.color = "rgb(18,183,137)" ;
						key[3].style.color = "rgb(18,183,137)" ;
						key[4].style.color = "rgb(18,183,137)" ;
						var val = document.getElementsByClassName("val");
						val[0].style.color = "rgb(255,102,102)" ;
						val[1].style.color = "rgb(255,102,102)" ;
						val[4].style.color = "rgb(255,102,102)" ;

				},
				created(){
					var _this = this;
					let userInfo = JSON.parse(window.ele_health_card.getUserInfo());
					this.userId = userInfo.userId;

					//console.log(_this.userId);
			  this.$http.get('http://183.136.187.224:18301/hangzhouApp/hghdc/getPerHealthRecord',{
					params:{
						userId:_this.userId
						//userId:"ec4a9301-1485-4c84-82eb-f1c642391929"
					}
			  })
			  .then(function(data){
					//console.log(data);
					if (data.data.buildFileOrg){
						_this.contents.建档机构 = data.data.buildFileOrg;
					}
					else{
						_this.contents.建档机构 = "无";
					}
					

					if (data.data.diabetesMark){
						_this.contents.糖尿病标志 = data.data.diabetesMark;
					}
					else{
						_this.contents.糖尿病标志 = "无";
					}
					

					if (data.data.fileNumber){
						_this.contents.档案编号 = data.data.fileNumber;
					}
					else{
						_this.contents.档案编号 = "无";
					}
					

					if (data.data.fileState){
						_this.contents.档案状态 = data.data.fileState;
					}
					else{
						_this.contents.档案状态 = "无";
					}

					if (data.data.hyperlipidemiaRiskGrade){
						_this.contents.高血脂高危等级 = data.data.hyperlipidemiaRiskGrade;
					}
					else{
						_this.contents.高血脂高危等级 = "无";
					}

					if (data.data.hypertensionMark){
						_this.contents.高血压标志 = data.data.hypertensionMark;
					}
					else{
						_this.contents.高血压标志 = "无";
					}

					if (data.data.manageOrg){
						_this.contents.管理机构 = data.data.manageOrg;
					}
					else{
						_this.contents.管理机构 = "无";
					}

					if (data.data.responDoctor){
						_this.contents.责任医生 = data.data.responDoctor;
					}
					else{
						_this.contents.责任医生 = "无";
					}
				})
		}

	}
</script>

<style scoaped>

#personal-doc .head{
	width: 100%;
}

#personal-doc .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#personal-doc .content{
	margin-top: 0.1rem;
}
#personal-doc .head span,#personal-doc .head label{
	line-height: 0.35rem;
}	
#personal-doc .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#personal-doc .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}
ul{
	list-style-type: none;
}
ul,li{
	margin:0rem;
	padding: 0rem;
}
#personal-doc .content ul li{
	display:block;
	line-height: 0.55rem;
	width: 100%;
	height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
#personal-doc .content ul li span.key{
	display:block;
	width: 30%;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.2rem;
	float: left;
	padding: 0rem;

}
#personal-doc .content ul li span.val{
	display:block;
	width: 50%;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.2rem;
	float: left;
}
</style>


