<!-- 个人既往史 -->
<template>
	<div id="personal-his">
		<!-- <div class="title">
			<router-link to ="/"><span><img :src="img1"></span></router-link><label>健康档案</label>
		</div> -->
		<div class="head">
			<span><img :src="img2"></span><label>个人既往史</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'personal-his',
		data(){
			return{
				userId:"",
				img2:require("../assets/jiwangshi.png"),
				contents:{
					"疾病史":"无",
					"药物过敏史":"无",
					"结核病史":"无",
					"过敏药物":"无",
					},
			}
		},
		mounted(){
					var head = document.getElementsByClassName("head");
					var content = document.getElementsByClassName("content");
					head[0].style.backgroundColor = "#fff";
					content[0].style.backgroundColor = "#fff";
					document.body.style.backgroundColor = "#f0f0f0";
				},
		created(){
			 var _this = this;
			 let userInfo = JSON.parse(window.ele_health_card.getUserInfo());
			 this.userId = userInfo.userId;

			 this.$http.get('http://183.136.187.224:18301/hangzhouApp/hghdc/getPerPastHistory',{
					params:{
						userId:_this.userId
						//userId:"ec4a9301-1485-4c84-82eb-f1c642391929"
						}
			  })
			 .then(function(data){
			  		//console.log(data);
			  	//anaphylaxis:过敏反应，drugAllergy:药物过敏反应，pasthistory:疾病史，tuberculosis:肺结核
				  	 if (data.data.anaphylaxis){
				  		_this.contents.过敏药物 = data.data.anaphylaxis;
				  	 }

				  	 if (data.data.drugAllergyHistory){
				  	  	_this.contents.药物过敏史 = data.data.drugAllergyHistory;
				  	  }

				  	if (data.data.pastHistory){
				  	  	_this.contents.疾病史 = data.data.pastHistory;
				  	  }

				  	  if (data.data.tuberculosisHistory){
				  	  	_this.contents.结核病史 = data.data.tuberculosisHistory;
				  	  }
			 })
		}
	}
</script>

<style scoaped>
#personal-his .head,.content{
	width: 100%;
}
#personal-his .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#personal-his .content{
	margin-top: 0.1rem;
}
#personal-his .head span,#personal-his .head label{
	line-height: 0.35rem;
}	
#personal-his .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#personal-his .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}
ul{
	list-style-type: none;
}
ul,li{
	margin:0rem;
	padding: 0rem;
}
#personal-his .content ul li{
	display:block;
	line-height: 0.55rem;
	width: 100%;
	height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
#personal-his .content ul li span.key{
	display:block;
	width: 1.2rem;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.2rem;
	float: left;
	padding: 0rem;

}
#personal-his .content ul li span.val{
	display:block;
	width: 1.2rem;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.35rem;
	float: left;
}
</style>


