<!-- 家族史 -->
<template>
	<div id="family-his">
		<!-- <div class="title">
			<router-link to ="/"><span><img :src="img1"></span></router-link><label>健康档案</label>
		</div> -->
		<div class="head">
			<span><img :src="img2"></span><label>家族史</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'family-his',
		data(){
			return{
				userId:"",
				img2:require("../assets/jiazu.png"),
				contents:{
					"父亲":"",
					"母亲":"",
					"兄弟姐妹":"",
					"家族遗传病史":"",
				},
			}
		},
		mounted(){

				var head = document.getElementsByClassName("head");
				var content = document.getElementsByClassName("content");
				head[0].style.backgroundColor = "#fff";
				content[0].style.backgroundColor = "#fff";
				document.body.style.backgroundColor = "#f0f0f0";

				},

		created(){
			var _this = this;
			 let userInfo = JSON.parse(window.ele_health_card.getUserInfo());
			 this.userId = userInfo.userId;

			this.$http.get('http://183.136.187.224:18301/hangzhouApp/hghdc/getFamilyHistory',{
					params:{
						userId:_this.userId
						//userId:"d69211da-3704-4f05-ae92-0a6d0388951c"
					}
			  }).then(function(data){
				 //console.log(data);
				 if (data.data.fatherName){
				 	_this.contents.父亲 = data.data.fatherName;
				 }
				 else{
				 	_this.contents.父亲 = "未记录";
				 }

				 if (data.data.motherName){
				 	_this.contents.母亲 = data.data.motherName;
				 }
				 else{
				 	_this.contents.母亲 = "未记录";
				 }

				 if (data.data.pastHistory){
				 	_this.contents.家族遗传病史 = data.data.pastHistory;
				 }
				 else{
				 	_this.contents.家族遗传病史 = "无";
				 }
				 
				 if (data.data.sibling){
				 	_this.contents.兄弟姐妹 = data.data.sibling;
				 }
				 else{
				 	_this.contents.兄弟姐妹 = "无";
				 }
				
			 })
		}
	}
</script>

<style scoaped>

#family-his .head,.content{
	width: 100%;
}
#family-his .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#family-his .content{
	margin-top: 0.1rem;
}
#family-his .head span,#family-his .head label{
	line-height: 0.35rem;
}	
#family-his .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#family-his .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}
ul{
	list-style-type: none;
}
ul,li{
	margin:0rem;
	padding: 0rem;
}
#family-his .content ul li{
	display:block;
	line-height: 0.55rem;
	width: 100%;
	height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
#family-his .content ul li span.key{
	display:block;
	width: 1.2rem;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.2rem;
	float: left;
	padding: 0rem;

}
#family-his .content ul li span.val{
	display:block;
	width: 1.5rem;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.35rem;
	float: left;
}
</style>


