<!-- 个人生活环境 -->
<template>
	<div id="personal-life">
		<!-- <div class="title">
			<router-link to ="/"><span><img :src="img1"></span></router-link><label>健康档案</label>
		</div> -->
		<div class="head">
			<span><img :src="img2"></span><label>生活环境</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'personal-life',
		data(){
			return{
				userId:"",
				img2:require("../assets/huanjing.png"),
				contents:{
					"家庭厕所类别":"其他",
					"家庭厨房排风设施类别":"其他",
					"家庭禽畜栏类别":"其他",
					"家庭燃料类型":"其他"
				},
			}
		},
		mounted(){
				var head = document.getElementsByClassName("head");
				var content = document.getElementsByClassName("content");
				head[0].style.backgroundColor = "#fff";
				content[0].style.backgroundColor = "#fff";
				document.body.style.backgroundColor = "#f0f0f0";
				},
		created(){
			//corralType:家庭禽畜栏类别,fuelType:家庭燃料类型，kitVentType:家庭厨房排风设施类别,toiletType:家庭厕所类别
				var _this = this;
				let userInfo = JSON.parse(window.ele_health_card.getUserInfo());
			    this.userId = userInfo.userId;

				 this.$http.get('http://183.136.187.224:18301/hangzhouApp/hghdc/getLiveEnviroment',{
					params:{
						userId:_this.userId
						//userId:"ec4a9301-1485-4c84-82eb-f1c642391929"
					}
			  })
				 .then(function(data){
					//console.log(data);
					if (data.data.corralType){
						_this.contents.家庭禽畜栏类别 = data.data.corralType;
					}

					if (data.data.fuelType){
						_this.contents.家庭燃料类型 = data.data.fuelType;
					}

					if (data.data.kitVentType){
						_this.contents.家庭厨房排风设施类别 = data.data.kitVentType;
					}

					if (data.data.toiletType){
						_this.contents.家庭厕所类别 = data.data.toiletType;
					}
				})
		}
	}
</script>

<style scoaped>
#personal-life .head,.content{
	width: 100%;
}
#personal-life .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#personal-life .content{
	margin-top: 0.1rem;
}
#personal-life .head span,#personal-life .head label{
	line-height: 0.35rem;
}	
#personal-life .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#personal-life .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}
ul{
	list-style-type: none;
}
ul,li{
	margin:0rem;
	padding: 0rem;
}
#personal-life .content ul li{
	display:block;
	line-height: 0.55rem;
	width: 100%;
	height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
#personal-life .content ul li span.key{
	display:block;
	width: 55%;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.2rem;
	float: left;
	padding: 0rem;

}
#personal-life .content ul li span.val{
	display:block;
	width: 0.8rem;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.35rem;
	float: left;
}
</style>


