<!-- 主界面入口 -->
<template>
	<div id="total-show">
			<div class="base-bac"></div>
			<div class="base-card">
				<div class="base-pic"><img :src="img.bg"></div>
				<div class="content">
					<div class="one">
						<ul>
							<li><label class="user">{{card.user}}</label></li>
							<li>
								<div class="box1">
							    	<div class="box1-1">
							    	<img :src="tag===1?img.man:img.woman"> <label>{{card.marry_info}}
									</label>
									</div>
								</div>
							</li>
							
						</ul>
					</div>
					<div class="two">
						<ul>
							<li class="ethnic"><img :src="img.ethnic"><label>民族</label><span>{{card.ethnic}}</span></li>
							<li class="birth"><img :src="img.birth"><label>出生日期</label><span>{{card.birth_day}}</span></li>
							<li class="blood"><img :src="img.blood"><label>血型</label><span>{{card.blood_type}}</span></li>
							
						</ul>	
					</div>
				</div>
			</div>
		<router-link to = "/health">
			<div class="personal-doc">
				<ul>
					<li><span class="icon1"><img :src="img.health">
					</span></li>
					<li><span class="line"></span></li>
					<li><span class="word">个人健康档案</span></li>
					<li><span class="icon2"><img :src="img.right"></span></li>
				</ul>
			</div>
		</router-link>
		<router-link to ="/history">
			<div class="personal-his">
				<ul>
					<li><span class="icon1"><img :src="img.jiwangshi">
					</span></li>
					<li><span class="line"></span></li>
					<li><span class="word">个人既往史</span></li>
					<li><span class="icon2"><img :src="img.right"></span></li>
				</ul>
			</div>
		</router-link>
		<router-link to ="/family">
			<div class="family-his">
				<ul>
					<li><span class="icon1"><img :src="img.jiazu">
					</span></li>
					<li><span class="line"></span></li>
					<li><span class="word">家族史</span></li>
					<li><span class="icon2"><img :src="img.right"></span></li>
				</ul>
			</div>
		</router-link>
		<router-link to ="/habbit">
			<div class="personal-life">
				<ul>
					<li><span class="icon1"><img :src="img.life">
					</span></li>
					<li><span class="line"></span></li>
					<li><span class="word">个人生活习惯</span></li>
					<li><span class="icon2"><img :src="img.right"></span></li>
				</ul>
			</div>
		</router-link>
		<router-link to ="/life">
		<div class="personal-hab">
			<ul>
				<li><span class="icon1"><img :src="img.env">
				</span></li>
				<li><span class="line"></span></li>
				<li><span class="word">生活环境</span></li>
				<li><span class="icon2"><img :src="img.right"></span></li>
			</ul>
		</div>
		</router-link>	 
	</div>
</template>

<script>
	export default{
		name:'total-show',
		data(){
			return{
				userId:"",
				img:{
					bg:require("../assets/shuiyin.png"),
					man:require("../assets/man.png"),
					woman:require("../assets/woman.png"),
					ethnic:require("../assets/ethnic.png"),
					birth:require("../assets/birth.png"),
					blood:require("../assets/blood.png"),
					card:require("../assets/card.png"),
					health:require("../assets/jiankang.png"),
					right:require("../assets/rightdir.png"),
					jiwangshi:require("../assets/jiwangshi.png"),
					jiazu:require("../assets/jiazu.png"),
					life:require("../assets/shenghuo.png"),
					env:require("../assets/huanjing.png"),
				},
				card:{
					user:"",
					sex:"",
					ethnic:"",
					birth_day:"",
					blood_type:"",
					//card_number:"",
					marry_info:"",
					//card_type:""
				},
				tag:1
			}
		},
		mounted(){
					document.body.style.backgroundColor = "#fff";
				},
		created(){
				var _this = this;
				let userInfo = JSON.parse(window.ele_health_card.getUserInfo());
				//  alert(userInfo.userId);
				this.userId = userInfo.userId;

			//this.userId = "ec4a9301-1485-4c84-82eb-f1c642391929";

			//bloodtType,born,gender:sex,marStatus,medCardCode,medNumber,name,nation
			 this.$http.get('http://183.136.187.224:18301/hangzhouApp/hghdc/getPerBasicInfo',{
					params:{
						 userId:_this.userId
						 //userId:"ec4a9301-1485-4c84-82eb-f1c642391929"
					}
			  }).then(function(data){
 					// console.log(data);
 					//data.data.name = null;
 					if (data.data.name === null){
 						var	nodoc = document.getElementById("total-show");
 						nodoc.innerHTML = "<img src='./static/none.png' id='none'/>";

 					}
 					else{
 					_this.card.user = data.data.name;
 					_this.card.sex = data.data.gender;
 					_this.card.ethnic = data.data.nation;
 					_this.card.birth_day = data.data.born;
 					_this.card.blood_type = data.data.bloodType;
 					_this.card.marry_info = data.data.marStatus;
 					if (_this.card.sex === "女性"){
						_this.tag = 0;
						return _this.tag;
					}
				}
 					
			 })
		}
	}	
</script>

<style scoaped>
#total-show{
	background-color: rgb(255,255,255);
}
.base-bac{
	 	position: relative;
		width: 100%;
		height: 0.8rem;
		border-radius: 0 0 19.5% 19.5%;
		background:-moz-linear-gradient(rgb(6,181,131),rgb(62,191,160));
	    background:-webkit-linear-gradient(rgb(6,181,131),rgb(62,191,160));
	    background:linear-gradient(rgb(6,181,131),rgb(62,191,160));
}
 .base-card,.personal-doc,.personal-his,.personal-life,.family-his,.personal-hab{
	border-radius: 0.1rem;
	-moz-box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	-webkit-box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	box-shadow: 0rem 0.035rem 0.135rem 0.02rem rgba(181,181,181,0.16);
	background-color: #fff;
}
 .base-card{
	position: relative;
	width: 92%;
	height: 2rem;
	margin: -0.6rem 0.15rem 0rem 0.15rem;
	z-index: 3;
}
 .base-pic{
	position: absolute;
	width: 45.98%;
	height: 1.57rem;
	margin: 0.22rem 0.155rem 0.21rem 1.755rem;
}
 .base-pic img{
	width: 98%;
	height: 1.57rem;
}
 .base-card .content .one ul li{
 	position: relative;
	float: left;
	height: 0.29rem;
	line-height: 0.29rem;
}
 .base-card .content .one{
	position: absolute;
	margin-top: 0.25rem;
	margin-left: 0.25rem;
}
 .base-card .content .one ul li .user{
	display: inline-block;
	position: absolute;
	white-space: nowrap;
	font-size: 0.22rem;
	font-weight: bold;
}
 .base-card .content .one .box1{
 	position: relative;
	width: 0.45rem;
	height: 0.16rem;
	line-height: 0.15rem;
	margin-left: 0.8rem;
	margin-top: 0.09rem;
	text-align: center;
	background-color: rgb(127,160 ,248);
	border-radius: 0.075rem;
}
.base-card .content .one .box1 .box1-1{
	width: 0.5rem;
	height: 0.16rem;
}
.base-card .content .one .box1 .box1-1 img{
	position: absolute;
	display: block;
	width: 0.1rem;
	height: 0.1rem;
	padding-left: 0.05rem;
	padding-top: 0.032rem;
	z-index: 2;
}
 .base-card .content .one .box1 .box1-1 label{
 	position: absolute;
 	width: 0.35rem;
	font-size: 0.1rem;
	color: rgb(255,255,255);	
	display: inline-block;
	margin-top:0.01rem;
	margin-bottom: 0.01rem;
	margin-left: -0.14rem;
}
 .base-card .content .one .box2{
	width: 0.55rem;
	height: 0.2rem;
	color: rgb(62,191,160);
	border-radius: 0.105rem;
	background:rgb(245,255,245);
	border: 1px solid rgb(49,188,152);
	margin-top: 0.29rem;
	margin-left: 1.09rem;
	font-size: 0.14rem;
	padding:0.025rem 0.07rem 0.03rem 0.07rem;
}
 .base-card .content .two{
 	width: 62%;
 	height: 0.96rem;
	padding:0.71rem 1.02rem 0.32rem 0.25rem;
}
 .base-card .content .two ul li{
	display: block;
	width: 100%;
	line-height: 0.13rem;
	margin-top: 0.2rem;
	font-size: 0.13rem;
	color: rgb(48,48,48);
}
 .base-card .content .two ul li.ethnic{
	margin-top: 0.04rem;
}
 .base-card .content .two ul li.ethnic img, .base-card .content .two ul li.birth img, .base-card .content .two ul li.blood img, .base-card .content .two ul li.card img{
	position: relative;
	width: 0.15rem;
	height: 0.13rem;
	vertical-align: middle;
}
 .base-card .content .two ul li.ethnic label, .base-card .content .two ul li.birth label, .base-card .content .two ul li.blood label, .base-card .content .two ul li.card label{
	display: inline-block;
	padding-left: 0.18rem;
	line-height: 0.13rem;
}
.base-card .content .two ul li.ethnic span, .base-card .content .two ul li.blood span{
		padding-left: 0.91rem;
		color: rgb(137,137,137);
		white-space: nowrap;
	}
 .base-card .content .two ul li.birth span, .base-card .content .two ul li.card span{
		display: inline-block;
		width: 0.2rem;
		padding-left: 0.64rem;
		color: rgb(137,137,137);
	}

ul{
	list-style-type: none;
}
ul,li{
	margin: 0rem;
	padding: 0rem;
}
.personal-doc,.personal-his,.personal-life,.family-his,.personal-hab{
	width: 92%;
	height: 0.65rem;
	margin: 0.15rem 0.15rem;
}
.personal-doc ul li span.line,.personal-his ul li span.line,.personal-life ul li span.line,.family-his ul li span.line,.personal-hab ul li span.line{
	display: inline-block;
	position: absolute;
	width: 0.86%;
	height: 0.35rem;
	margin-left: 1.15rem;
	margin-top: 0.15rem;
	background-color: rgb(238,238,238);

}
.personal-doc ul li,.personal-his ul li,.personal-life ul li,.family-his ul li,.personal-hab ul li{
	float: left;
	height: 0.45rem;
}
.personal-doc ul li span.icon1,.personal-his ul li span.icon1,.personal-life ul li span.icon1,.family-his ul li span.icon1,.personal-hab ul li span.icon1{
	display: inline-block;
	position: absolute;
	width: 12%;
	height: 0.45rem;
}
.personal-doc ul li span.icon1 img,.personal-his ul li span.icon1 img,.personal-life ul li span.icon1 img,.family-his ul li span.icon1 img,.personal-hab ul li span.icon1 img{
	position: absolute;
	width: 98%;
	height: 0.45rem;
	margin-left: 0.25rem;
	margin-top: 0.1rem;
}
.personal-doc ul li span.word,.personal-his ul li span.word,.personal-life ul li span.word,.family-his ul li span.word,.personal-hab ul li span.word{
	display: inline-block;
	position: absolute;
	width: 28.4%;
	line-height: 0.16rem;
	white-space: nowrap;
	margin-left: 1.5rem;
	padding-top:0.205rem;
	font-size: 0.17rem;
	color: rgb(109,109,109);
}
.personal-doc ul li span.icon2,.personal-his ul li span.icon2,.personal-life ul li span.icon2,.family-his ul li span.icon2,.personal-hab ul li span.icon2{
		display: inline-block;
		position: absolute;
		width: 4.34%;
		height: 0.45rem;
}
.personal-doc ul li span.icon2 img,.personal-his ul li span.icon2 img,.personal-life ul li span.icon2 img,.family-his ul li span.icon2 img,.personal-hab ul li span.icon2 img{
	position: absolute;
	width: 70%;
	height: 0.14rem;
	margin-left: 3.1rem;
	padding-top: 0.245rem;
}
#none{
	width: 66.67%;
	height:2.5rem;
	display: block;
	margin: 50% auto;
}

</style>


