<!-- 个人生活习惯 -->
<template>
	<div id="personal-hab">
		<!-- <div class="title">
			<router-link to ="/"><span><img :src="img1"></span></router-link><label>健康档案</label>
		</div> -->
		<div class="head">
			<span><img :src="img2"></span><label>个人生活习惯</label>
		</div>
		<div class="content">
			<ul>
				<li v-for = "(val,key) in contents">
				<span class="key">{{key}}</span>
				<span class="val">{{val}}</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'personal-hab',
		data(){
			return{
				userId:"",
				img2:require("../assets/shenghuo.png"),
				contents:{
					"吸烟状况":"",
					"吸烟时长":"",
					"饮酒频率":"",
					"饮食习惯名称":"",
					"结果描述":""
				},
			}
		},
		mounted(){
				var head = document.getElementsByClassName("head");
				var content = document.getElementsByClassName("content");
				head[0].style.backgroundColor = "#fff";
				content[0].style.backgroundColor = "#fff";
				document.body.style.backgroundColor = "#f0f0f0";

				},

		 created(){
		 	//drinkFrequency:饮酒频率，eatHabits:饮食习惯名称，isSmoke:吸烟状况，resultDescription:结果描述,smokeTime:吸烟时长
			    	var _this = this;
					let userInfo = JSON.parse(window.ele_health_card.getUserInfo());
					this.userId = userInfo.userId;

					this.$http.get('http://183.136.187.224:18301/hangzhouApp/hghdc/getLifeStyle',{
						params:{
						   userId:_this.userId
		 				   //userId:"d69211da-3704-4f05-ae92-0a6d0388951c"
		 			}
		 	  })
		 			.then(function(data){
		 			//console.log(data);
		 			if (data.data.drinkFrequency){
		 				_this.contents.饮酒频率 = data.data.drinkFrequency;
		 			}
		 			else{
		 				_this.contents.饮酒频率 = "无";
		 			}
		 			

		 			if (data.data.eatHabits){
		 				_this.contents.饮食习惯名称 = data.data.eatHabits;
		 			}
		 			else{
		 				_this.contents.饮食习惯名称 = "无";
		 			}
		 			

		 			if (data.data.isSmoke){
		 				_this.contents.吸烟状况 = data.data.isSmoke;
		 			}
		 			else{
		 				_this.contents.吸烟状况 = "无";
		 			}
		 			

		 			if (data.data.resultDescription){
		 				_this.contents.结果描述 = data.data.resultDescription;
		 			}
		 			else{
		 				_this.contents.结果描述 = "无";
		 			}

		 			if (data.data.smokeTime){
		 				_this.contents.吸烟时长 = data.data.smokeTime;
		 			}
		 			else{
		 				_this.contents.吸烟时长 = "无";
		 			}
		 			
		 	})
		 }
	}
</script>

<style scoaped>
#personal-hab .head,.content{
	width: 100%;
}
#personal-hab .head{
	height: 0.6rem; 
	margin-top: 0.005rem;
}
#personal-hab .content{
	margin-top: 0.1rem;
}
#personal-hab .head span,#personal-hab .head label{
	line-height: 0.35rem;
}	
#personal-hab .head span img{
	position: absolute;
	width: 0.35rem;
	height: 0.35rem;
	padding-top: 0.125rem;
	padding-left: 0.2rem;
}
#personal-hab .head label{
	display: inline-block;
	position: absolute;
	font-size: 0.17rem;
	padding-top: 0.125rem;
	padding-left: 0.75rem;
	color: rgb(48,48,48);
}
ul{
	list-style-type: none;
}
ul,li{
	margin:0rem;
	padding: 0rem;
}
#personal-hab .content ul li{
	display:block;
	line-height: 0.55rem;
	width: 100%;
	height: 0.55rem;
	font-size: 0.15rem;
	border-bottom: 1px solid #f0f0f0;
}
#personal-hab .content ul li span.key{
	display:block;
	width: 1.2rem;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.2rem;
	float: left;
	padding: 0rem;

}
#personal-hab .content ul li span.val{
	display:block;
	width: 1.2rem;
	line-height: 0.35rem;
	margin-top: 0.12rem;
	margin-left: 0.35rem;
	float: left;
}
</style>


